@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.slick-dots {
  display: flex;
  justify-content: center;
  position: relative;
  top: -21rem;
}
.slick-dots li {
  height: 16px;
  width: 16px;
}
.slick-dots li button {
  display: none;
}
.slick-dots li:hover button {
  display: none;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .slick-dots {
    top: -3rem;
  }
}
@media (min-width: 1025px) {
  .slick-dots {
    top: -13rem;
  }
}

.astudio__slick-slider-container {
  margin-top: 50px;
}
.astudio__slick-slider-container .astudio__slick-slider {
  display: block;
}
.astudio__slick-slider-container .astudio__slick-slider-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 14px;
}
.astudio__slick-slider-container .astudio__slick-slider-wrapper-b1 img {
  height: 389px;
  width: 389px;
}
.astudio__slick-slider-container .astudio__slick-slider-wrapper-b2 {
  max-width: 412px;
  margin-right: 38px;
}
.astudio__slick-slider-container .astudio__slick-slider-wrapper-b2 h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 24px;
  line-height: 30px;
  color: #000000;
}
.astudio__slick-slider-container .astudio__slick-slider-wrapper-b2 p {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 16px;
  line-height: 25px;
  color: #565656;
}
.astudio__slick-slider-container .astudio__slick-slider-wrapper-b2-dotImg {
  background-image: url(".../../../../../images/Dot.png");
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__slick-slider-container .astudio__slick-slider-wrapper {
    flex-direction: row;
  }
}
@media (min-width: 1025px) {
  .astudio__slick-slider-container .astudio__slick-slider-wrapper {
    flex-direction: row;
    margin-left: 0;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__slick-slider-container .astudio__slick-slider-b3 {
    display: none;
  }
}
.astudio__slick-slider-container .astudio__slick-slider-roundImg {
  height: 184px;
  width: 184px;
  position: relative;
  top: -25rem;
  z-index: -5;
  right: -3rem;
  background-image: url(".../../../../../images/Ellipse 93.png");
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__slick-slider-container .astudio__slick-slider-roundImg {
    display: none;
  }
}
@media (min-width: 1025px) {
  .astudio__slick-slider-container .astudio__slick-slider-roundImg {
    top: 0;
    right: 5rem;
  }
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs {
  position: relative;
  top: -5rem;
  border-radius: 50%;
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs a {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 4em;
  height: 4em;
  margin: -2em;
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl1 {
  transform: rotate(261deg) translate(-8em) rotate(96deg);
  top: 50px;
  position: relative;
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl2 {
  transform: rotate(80deg) translate(14em) rotate(-81deg);
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl3 {
  transform: rotate(63deg) translate(15em) rotate(-64deg);
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl4 {
  transform: rotate(199deg) translate(-16.8em) rotate(-205deg);
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl5 {
  transform: rotate(-12deg) translate(19em) rotate(14deg);
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl6 {
  transform: rotate(322deg) translate(21em) rotate(-325deg);
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl7 {
  transform: rotate(116deg) translate(-22em) rotate(-118deg);
}
.astudio__slick-slider-container .astudio__slick-slider_roundImgs-bl8 {
  transform: rotate(116deg) translate(-19em) rotate(-120deg);
}
@media (min-width: 1025px) {
  .astudio__slick-slider-container .astudio__slick-slider_roundImgs {
    top: 0;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__slick-slider-container .astudio__slick-slider {
    margin-top: 80px;
  }
}
@media (min-width: 1025px) {
  .astudio__slick-slider-container .astudio__slick-slider {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__slick-slider-container {
    margin-top: 0;
  }
}
@media (min-width: 1025px) {
  .astudio__slick-slider-container {
    margin-top: 0;
  }
}